import Oneone from './oneone';
import React, { Component } from 'react';

import img1 from '../img/Mask_Group_2.png';
import img2 from '../img/Mask_Group_3.png';
import img3 from '../img/Mask_Group_4.png';
import img4 from '../img/Mask_Group_5.png';
import img5 from '../img/Mask_Group_6.png';
import img6 from '../img/Mask_Group_7.png';
import img7 from '../img/Mask_Group_8.png';
import img8 from '../img/Mask_Group_9.png';



class Game extends Component {
    state = {  
        row1: [
            { number: "n1", name:"1111", img: img1 },
            { number: "n2", name:"2222", img: img2 },
            { number: "n3", name:"3333", img: img3 },
            { number: "n4", name:"4444", img: img4 },
        ],
        row2: [
            { number: "n5", name:"5555", img: img5 },
            { number: "n6", name:"6666", img: img6 },
            { number: "n7", name:"7777", img: img7 },
            { number: "n8", name:"8888", img: img8 },
        ],
        row3: [
            { number: "n9", name:"1111", img: img1 },
            { number: "n10", name:"2222", img: img2 },
            { number: "n11", name:"3333", img: img3 },
            { number: "n12", name:"4444", img: img4 },
        ],
        row4: [
            { number: "n13", name:"5555", img: img5 },
            { number: "n14", name:"6666", img: img6 },
            { number: "n15", name:"7777", img: img7 },
            { number: "n16", name:"8888", img: img8 },
        ],
        new : false
    }
    
    render() {  
        return ( 
            <React.Fragment>
                <h1 className="game-name">MEMORY CARD GAME</h1>
            <div className="box">
                <div className="bigRow">
                    <div className="row">
                    {
                        this.state.row1.map(one => <div className="row">
                        <Oneone name={one.name} front={one.number+"-front"} back={one.number+"-back"} img={one.img} new={this.state.new} />
                    </div>)
                    }
                </div>
                <div className="row">
                    {
                        this.state.row2.map(one => <div className="row">
                        <Oneone name={one.name} front={one.number+"-front"} back={one.number+"-back"} img={one.img} new={this.state.new} />
                    </div>)
                    }
                </div>
                <div className="row">
                    {
                        this.state.row3.map(one => <div className="row">
                        <Oneone name={one.name} front={one.number+"-front"} back={one.number+"-back"} img={one.img} new={this.state.new} />
                    </div>)
                    }
                </div>
                <div className="row">
                    {
                        this.state.row4.map(one => <div className="row">
                        <Oneone name={one.name} front={one.number+"-front"} back={one.number+"-back"} img={one.img} new={this.state.new} />
                    </div>)
                    }
                </div>
                
                </div>
                
                
            </div>
            <h1 className="game-name"><span onClick={this.state.new=true}>NEW</span></h1>
            </React.Fragment>
        );
    }
}

export default Game;